import { useEffect, useState } from "react";
import axios from "axios";

const POPULAR_API = "https://api.themoviedb.org/3/movie/popular?api_key=e0eb34f3bddc89aabdffd507b370db1e&language=en-US&page=1"
const IMG_API = "https://image.tmdb.org/t/p/w1280";


const PopularMovies = () =>{
    const [ movies, setMovies] = useState([]);

    useEffect(() => {
        axios
            .get(POPULAR_API)
            .then((res) => {
                setMovies(res.data.results)
                console.log(res.data);
            })
            .catch((err) => {
                console.log(err);
            })
    },[]);

    function contverter(str) {
    var year = "";
    var day = "";
    var month = "";

    var object = {
        "01": "January",
        "02": "Februery",
        "03": "March",
        "04": "April",
        "05": "May",
        "06":"June",
        "07": "July",
        "08": "August",
        "09": "September",
        "10": "October",
        "11": "Novemebr",
        "12": "December"
    }
    
    for(var i = 0; i <= str.length - 7; i++) {
        year += str[i];
    } 
    // console.log("Done")
    for(var j = 5; j <= str.length - 4; j++) {
        month += str[j];
    }
    // console.log(month)
    for(var k = 8; k <= str.length - 1; k++) {
        day += str[k];
    }
    // console.log(day)
    if(object.hasOwnProperty(month)) {
        // console.log(object[month])
    }
    return object[month] + " " + day + ", " + year;
}
console.log(contverter("2021-12-10"));



    return (
        <div className="popular-movies-container">
            <div className="popular-movies-h2">
                <h2>What's Popular</h2>
            </div>
            <div className="movies-container">
                {movies.map((movies, idx) => {
                    return (
                        <div key={idx} className="movie-container">
                            <div className="movie-image">
                                <a href={`/TMDB-Project/movies/${movies.id}`}><img src={IMG_API + movies.poster_path} alt={movies.title}/></a>

                            </div>
                            <div className="movie-info">
                                <a href={`/TMDB-Project/movies/${movies.id}`}><h4>{movies.title}</h4></a>
                                <p>{movies.release_date}</p>
                            </div>
                        </div>    
                    )    
                })}

            </div>

        </div>
    )
}
export default PopularMovies;